@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
*, *:after, *:before {
  box-sizing: border-box;
}
.ibg {
  background: no-repeat center center;
  background-size: cover;
  img {
    display: none;
  }
} 
body, html {
  font-family: 'Raleway', sans-serif;
  color: #222222;
  line-height: 1.2;
  background: #f5f5f5;
}
.container {
  // width: 100%;
  // padding: 0 15px;
  // @media (min-width: 1400px) {
  //   padding: 0 calc(50% - 685px);
  // }
  width: 100%;
  max-width: 1430px;
  padding: 0 15px;
  margin: 0 auto;
}
a {
  color: #ff5252;
  opacity: 1;
  transition: opacity 0.2s;
  &:hover {
    opacity: 0.6;
  }
}

.crumbs {
  margin-top: 20px;
  span:not(:last-child) {
    &:after {
      content: ' /';

    }
  }
  a {
    text-decoration: none;
  }
 
}
.h1, .h2, .h3 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.h1 {
  font-size: 40px;
}
.h2 {
  font-size: 30px;
}
.h3 {
  font-size: 20px;
}

.text {
  font-size: 14px;
  color: #666666;
}

.homapage {
  text-align: center;
  .h1 {
    position: relative;
    margin-bottom: 0.8em;
    &:after {
      content: '';
      width: 100px;
      height: 1px;
      background: rgba(135, 135, 135, 0.5);
      position: absolute;
      bottom: -0.23em;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
  & > .text {
    max-width: 550px;
    margin: 0 auto;
  }
}

$grid-window-width: 960px;
.grid {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
  &__card {
    position: relative;
    display: block;
    width: 100%;
    margin: 15px 0;
    font-size: 12px;
    color: #ffffff;
    order: 2;
    @media (min-width: 850px) {
      width: calc(50% - 30px);
      margin: 15px;
    }
    @media (min-width: 1250px) {
      width: calc(33.3333% - 30px);
    }
    &:before {
      content: '';
      display: block;
      padding-top: 73.25%;
      height: 100%;
      background: radial-gradient(transparent, #000);
      transition: opacity 0.4s;
      opacity: 0.6;
      @media (min-width: 400px) {
        padding-top: 63.25%;
      }
      @media (min-width: 450px) {
        padding-top: 56.25%;
      }
      @media (min-width: $grid-window-width) {
        opacity: 0.4;
      }
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.7;
      background: #9e5406;
      transition: 0.4s;
      @media (min-width: $grid-window-width) {
        opacity: 0;
      }
    }
    &:hover {
      opacity: 1;
    }
    &:hover::before{
      opacity: 0.6;
    }
    &:hover::after{
      opacity: 0.7;
    }
    &:hover &__wrapper:after {
      width: calc(100% - 4em);
      opacity: 1;
    }
    &:hover &__wrapper:before {
      height: calc(100% - 4em);
      opacity: 1;
    }
    &:hover &__wrapper {
      h2, p {
        transform: translate(0, 0);
        opacity: 1;
      }
    }
    &__wrapper {
      padding: 2em;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      &:after, &:before {
        content: '';
        position: absolute;
        display: block;
        opacity: 1;
        transition: 0.4s;
        background: transparent;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: 2px solid #fff;
        @media (min-width: $grid-window-width) {
          opacity: 0;
        }
      }
      &:after {
        width: calc(100% - 4em);
        height: calc(100% - 4em);
        border-right-color: transparent;
        border-left-color: transparent;
        @media (min-width: $grid-window-width) {
          width: 0;
        }       
      }
      &:before {
        width: calc(100% - 4em);
        height: calc(100% - 4em);
        border-top-color: transparent;
        border-bottom-color: transparent;
        @media (min-width: $grid-window-width) {
          height: 0;
        } 
      }
      h2 {
        transform: translate(0, 0%);
        opacity: 1;
        transition: 0.4s;
        margin: 0 15px 0.5em;
        max-width: 100%;
        @media (max-width: 400px) {
          font-size: 15px;
        }
        @media (min-width: $grid-window-width) {
          transform: translate(0, -100%);
          opacity: 0;
        }
      }
      p {
        transform: translate(0, 0%);
        opacity: 1;
        transition: 0.4s;
        margin: 0 auto;
        max-width: 95%;
        @media (min-width: $grid-window-width) {
          transform: translate(0, 100%);
          opacity: 0;
        }
      }
    }
  }
  &__card.order-1 {
    order: 1;
  }
  &__card.order-2 {
    order: 2;
  }
  &__card.order-3 {
    order: 3;
  }
  &__card.order-4 {
    order: 4;
  }
  &__card.order-5 {
    order: 5;
  }
  &__card.order-6 {
    order: 6;
  }
} 

.post {
  .h1 {
    margin-top: 5px; 
  }
  img {
    max-width: 100%;
    display: block;
    border-radius: 15px;
    @media (min-width: 1040px) {
      max-width: 1000px;
    }
  }
}

